import template from './editable-table.html';

function editableTableDirective() {
  return {
    restrict: 'E',
    scope: {
      data: '=',
      addRow: '=',
    },
    template: template,
    replace: true,
    controller: editableTableController,
    controllerAs: 'vm',
  }
}

class editableTableController {
  constructor($scope, $element, $attrs) {
    'ngInject';

    this.data = $scope.data;
    this.addRow = $scope.addRow;
    this.data_clone = angular.copy(this.data, {});
  }

  edit(rowIndex) {
    if (this.checkError()) return;
    this.editIndex = rowIndex;
  }

  switch(rowIndex, colIndex) {
    let status = !!this.data_clone.body[rowIndex][colIndex].status;
    this.data_clone.body[rowIndex][colIndex].status = !status;
  }

  save() {
    if (this.checkError()) return;
    angular.copy(this.data_clone, this.data);
    this.editIndex = null;
  }

  delete(rowIndex) {
    this.data_clone.body.splice(rowIndex, 1);
    angular.copy(this.data_clone, this.data);
  }

  add() {
    if (this.checkError()) return;
    angular.merge(this.data_clone, this.data);
    if (this.data_clone.body.length > this.data.body.length) {
      angular.copy(this.data_clone, this.data);
    };
    const rowNum = this.data_clone.body.length;
    this.data_clone.body.push(angular.copy(this.addRow.body, []));
    this.editIndex = rowNum;
  }

  checkError() {
    let error = false;
    this.errorInfo = false;
    if (typeof(this.editIndex) !== 'number') return false;
    angular.forEach(this.data_clone.body[this.editIndex], v => {
      let value;
      v.value && (value = v.value.toString().trim());
      if (v.editable && !v.type && !v.options && !value && v.required) {
        error = true;
        this.errorInfo = true;
        return;
      }
    });
    return error;
  }
}

export default editableTableDirective;



